import React, { useState, useEffect } from 'react';
import { Redirect, withRouter } from 'react-router-dom';
import cookie from 'react-cookies';

import Switch from "react-switch";
import Modal from '../../components/Modal';
import Achievement from '../../components/Achievement';

const axios = require('axios');

function EditProfil({ history }) {


    const [modal, setModal] = useState({
        show: false,
        message: ""
    });
    function toggleModal(clean = false) {
        setModal({
            show: !modal.show,
            message: (clean) ? modal.message : ""
        })
    }

    function sendProfil() {
        console.log('sendProfil');
        setModal({
            show: true,
            message: ''
        });
        axios.post('../api/updatePlayer', {
            user: user,
        })
            .then(function (response) {
                setModal({
                    show: true,
                    message: response.data.message
                });
                if (response.data.redirect) {

                    setTimeout(() => {
                        cookie.remove('login');
                        cookie.remove('user');
                        cookie.remove('bets');
                        history.push('/')

                    }, 3000)
                }
                setTimeout(() => {
                    setModal({
                        show: false,
                        message: ''
                    });
                }, 4500)
            })
            .catch(function (error) {
                console.log(error);
            });

    }
    console.log(cookie.load('user'));
    const [userName, setUserName] = useState(cookie.load('user').login);
    const [user, setUser] = useState(null);

    if (user != null) {
        console.log('USER !!!!!', user)
        return (
            <div className="Profil">
                <Modal modal={modal} toggleModal={toggleModal}></Modal>
                <h2>{userName}</h2>
                <div className="user flex two center">

                    <div className="name ">
                        <label htmlFor="pseudo">Pseudo :
                            <input type="text" id="pseudo" defaultValue={user.name} onBlur={(event) => {
                                user.name = (event.target.value) ? event.target.value : user.name;
                                cookie.save('user', user, { path: '/' });
                                setUser(user);
                            }} />
                            <div className="alert">
                                Ton pseudo n'est pas ton login
                            </div>
                        </label>
                    </div>
                    <div className="password">
                        <label htmlFor="password">Password :
                            <input type="password" id="password" placeholder="*********" onBlur={(event) => {
                                user.mdp = (event.target.value) ? event.target.value : user.mdp;
                                cookie.save('user', user, { path: '/' });
                                setUser(user);
                            }} />
                            <div className="alert">
                                Si tu change ton password, tu sera deconnect
                                </div>
                        </label>

                    </div>
                    <div className="email">
                        <label htmlFor="email">Email :
                            <input type="text" id="email" defaultValue={user.mail} onBlur={(event) => {
                                user.mail = (event.target.value) ? event.target.value : user.mail;
                                cookie.save('user', user, { path: '/' });
                                setUser(user);
                            }} />
                            <div className="alert">
                                Soit avertie de l'avanc du projet
                                </div>
                        </label>
                    </div>
                    <div className="btag">
                        <label htmlFor="btag">Btag :
                            <input type="text" id="btag" defaultValue={user.btag} onBlur={(event) => {
                                user.btag = (event.target.value) ? event.target.value : user.btag;
                                cookie.save('user', user, { path: '/' });
                                setUser(user);
                            }} />
                            <div className="alert">
                                Sans btag pas de recompense possible
                                </div>
                        </label>
                    </div>
                    <div className="visibility">
                        Profil Public : (A venir)<br />
                        <Switch
                            onChange={() => {
                                user.visibility = (user.visibility) ? !user.visibility : true;
                                cookie.save('user', user, { path: '/' });
                                setUser(user);
                            }}
                            checked={(user.visibility) ? user.visibility : false}
                            onColor={'#cc682d'}
                            activeBoxShadow={'0 0 2px 3px #cc682d'}
                        ></Switch>

                    </div>
                    <div>IMG (A venir)</div>
                    <div className="loveTeam">
                        LoveTeam :

                            <select>
                            <option value={user.loveTeam}>{user.loveTeam} </option>
                        </select>
                    </div>
                    <div>

                    </div>
                    <div className="submit">
                        <div className="btn button" onClick={sendProfil}>
                            Valider
                        </div>
                    </div>

                </div>
                <h2>Haut Fait</h2>
                <Achievement user={user} />

            </div>
        )
    }
    else if (userName != null) {
        axios.get(`../api/getPlayer/${userName}`)
            .then(function (response) {
                console.log('RESPONSE !!!!', response);
                setUser(response.data);
            })
            .catch(function (error) {
                console.log(error);
            });
        return (<div></div>)
    }
    else {
        return (<div></div>)
    }






}



export default withRouter(EditProfil);