import React, { useState, useRef } from 'react';
import { Link, withRouter } from 'react-router-dom';
import cookie from 'react-cookies';

import openSocket from 'socket.io-client';
import { isRegExp } from 'util';
import Login from '../Login';

const socket = openSocket('163.172.159.99:5001');

console.log(document.URL);

function Chat({ match }) {


    socket.emit('sendLogin', cookie.load('user'));

    const inputMessage = useRef();
    function sendMessage() {
        socket.emit('sendMessage', inputMessage.current.value, cookie.load('user'));
        inputMessage.current.value = "";
        inputMessage.current.focus();
    }

    return (<div className="Chat">

        <h2>Chat</h2>
        <div className="flex">
            <ListUser></ListUser>
            <ZoneMessage></ZoneMessage>
        </div>
        <div className="ZoneSend">
            <div className="btn" onClick={
                () => {
                    sendMessage();
                }
            }>
                send
            </div>
            <input type="text" ref={inputMessage} onKeyPress={
                (event) => {
                    if (event.key == 'Enter') {
                        sendMessage();
                    }
                }
            } />

        </div>

    </div>)

}
function ListUser() {
    const [userList, setUserList] = useState(null);
    socket.on('newUser', (list) => {
        console.log('newUser');
        setUserList(list);
    })
    let LiUsers = null
    if (userList != null) {

        LiUsers = userList.map((user) => {
            return (<li key={useState.name}>{user.name}</li>)
        })

    }
    return (
        <ul className="ListUser">
            {LiUsers}
        </ul>)
}

function ZoneMessage() {
    const [messages, setMessages] = useState(null);
    socket.on('newMessages', (listMessages) => {
        console.log('newMessage');
        setMessages(listMessages);
        var objDiv = document.querySelector('.ZoneMessage');
        console.log(objDiv);
        objDiv.scrollTop = objDiv.scrollHeight;
    })
    function timeConverter(UNIX_timestamp) {
        var a = new Date(UNIX_timestamp);
        var months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
        var year = a.getFullYear();
        var month = months[a.getMonth()];
        var date = a.getDate();

        var hour = (a.getHours() < 10) ? '0' + a.getHours() : a.getHours();
        var mins = (a.getMinutes() < 10) ? '0' + a.getMinutes() : a.getMinutes();
        var time = date + ' ' + month + ' ' + year + ' - ' + hour + ':' + mins;
        return time;
    }

    let Messages = null
    if (messages != null) {
        Messages = messages.map((message, key) => {
            if (message.message != null) {
                return (
                    <li key={key} className="LiMessage">
                        <div className="name">
                            {message.user.name}
                        </div>
                        <div className="date">
                            {timeConverter(message.date)}
                        </div>
                        <div className="message">
                            {message.message}
                        </div>
                    </li>
                )
            }

        })

    }
    return (
        <ul className="ZoneMessage">
            {Messages}
        </ul>)
}

export default withRouter(Chat);