import React, { useState } from 'react';
import { withRouter } from 'react-router-dom';
import cookie from 'react-cookies';

import Match from "../../components/Match"

import ReactCardFlip from 'react-card-flip';

const axios = require('axios');

const keyTwitch = 'as9drzlxfqojhjzeomk8qtrrikcbss';

function Team({ match, history }) {
    const [team, setTeam] = useState(null);

    const [idteam] = useState(match.params.idteam);

    if (team === null) {
        axios.get(`https://api.overwatchleague.com/teams/${idteam}`)
            .then(function (response) {
                setTeam(response.data);
            })
            .catch(function (error) {
                console.log(error);
            });
        return (
            <div>
                <h2>Teams</h2>
            </div>
        );
    } else {

        const Matchs = team.schedule.map(match => {
            return <Match match={match} key={match.id} />
        })
        const Players = team.players.map(player => {

            return <Player player={player} key={player.id} />
        })

        return (
            <div className="Team">
                <h2>{team.name}</h2>
                <div className="body">
                    {
                        (cookie.load('login')) ?
                            <div className="loveTeam" onClick={
                                () => {
                                    axios.post('../api/loveTeam', {
                                        teamId: team.abbreviatedName,
                                        user: cookie.load('user').name
                                    })
                                        .then(function (response) {
                                            cookie.save('user', response.data, { path: '/' });
                                            history.push(history.location.pathname)
                                        })
                                        .catch(function (error) {
                                            console.log(error);
                                        });


                                }
                            }>
                                {
                                    (cookie.load('user')) ?
                                        <div className={
                                            (cookie.load('user').loveTeam) ?
                                                (cookie.load('user').loveTeam === team.abbreviatedName) ? 'btnLoveTeam this'
                                                    : 'btnLoveTeam'
                                                : 'btnLoveTeam empty'
                                        }>
                                            
                        </div>
                                        : ''
                                }

                            </div>
                            : ''
                    }
                    {/* <div className="img">
                <img src={team.logo} alt=""/>
            </div> */}
                    <h3>Planning</h3>
                    <div className="shedule flex center">
                        {Matchs}
                    </div>
                    <h3>Roster</h3>

                    <div className="roster flex center five">
                        {Players}
                    </div>
                </div>
            </div>
        );
    }
}

function Player({ player }) {

    const [infoPlayer, setInfoPlayer] = useState(null);
    const [hover, setHover] = useState(false)

    if (infoPlayer == null) {
        axios.get(`https://api.overwatchleague.com/players/${player.id}`)
            .then(function (response) {
                setInfoPlayer(response.data.data.player);
            })
            .catch(function (error) {
                console.log(error);
            });
        return (
            <ReactCardFlip isFlipped={hover} key={player.name}>
                <div key="front" className="player" onMouseEnter={
                    () => {
                        setHover(!hover);
                    }
                }
                    onMouseLeave={
                        () => {
                            setHover(!hover);
                        }
                    }
                >
                    <div className="playerName">{player.name}</div>
                    <div className="playerImg">
                        <img src={player.headshot} alt="" />
                    </div>
                </div>

                <div key="back">
                    This is the back of the card.
                <button onClick={
                        () => {
                            setHover(!hover);
                        }
                    }>Click to flip</button>
                </div>
            </ReactCardFlip>
        );
    }
    else {
        let urlTwitch = infoPlayer.accounts.find((account) => {
            return account.accountType === "TWITCH";
        })

        const Heros = (player.attributes.heroes) ? player.attributes.heroes.map((hero) => {
            return (<div className={`ohi ${hero.name}`} key={hero.name}></div>)
        }):null;
        return (
            <ReactCardFlip isFlipped={hover}>
                <div key="front" className="player" onMouseEnter={
                    () => {
                        setHover(!hover);
                    }
                }
                >
                    <div className="playerImg">
                        <img src={player.headshot} alt="" />
                    </div>
                    <TwitchWidget urlTwitch={urlTwitch} name={player.name}></TwitchWidget>
                </div>
                <div key="back" className="player" onMouseLeave={
                    () => {
                        setHover(!hover);
                    }
                }>
                    <div className="playerName">{player.givenName} "{player.name}" {player.familyName} </div>

                    <div className="playerInfo">
                        <div className="heroPlayed">
                            {(Heros != null)? Heros.slice(0,4): Heros}
                        </div>
                    </div>
                    <TwitchWidget urlTwitch={urlTwitch} name={player.name}></TwitchWidget>
                </div>
            </ReactCardFlip>



        );
    }

}
function TwitchWidget({ urlTwitch, name }) {

    const idTwitch = (urlTwitch !== undefined) ? urlTwitch.value.split('/').pop() : null;

    const [online, setOnline] = useState(false);

    if (!online && urlTwitch !== undefined) {
        // `https://api.twitch.tv/kraken/streams/${idTwitch}?client_id=${keyTwitch}`
        axios.get(`https://api.twitch.tv/kraken/streams/${idTwitch}?client_id=${keyTwitch}`)
            .then(function (response) {
                if (response.data.stream != null) {
                    setOnline(response.data.stream);
                }

            })
            .catch(function (error) {
                console.log(error);
            });
    }

    if (urlTwitch) {
        return (
            <a href={urlTwitch.value} target="_blank" rel="noopener noreferrer" className={(online.game) ? "playerTwitch live" : "playerTwitch"}>
                {name}
                {online.game}
            </a>
        );
    } else {
        return (
            <div className={(online.game) ? "playerTwitch live" : "playerTwitch"}>
                {name}
            </div>
        );
    }


}



export default withRouter(Team);